let catalogCardSlidersArray = []

function catalogCardSliders() {
    $(".catalog-card__media").each(function(index, el) {
        $(el).attr("data-slider-id", `${index}`)
        catalogCardSlidersArray[index] = new Swiper(`.catalog-card__media[data-slider-id="${index}"] .swiper-container`, {
            slidesPerView: 1,
            spaceBetween: 10,
            navigation: {
                nextEl: `.catalog-card__media[data-slider-id="${index}"] .swiper-button-next`,
                prevEl: `.catalog-card__media[data-slider-id="${index}"] .swiper-button-prev`,
            },
            pagination: {
                el: `.catalog-card__media[data-slider-id="${index}"] .swiper-pagination`,
                type: 'bullets',
                clickable: true,
            },
        })
    })
}

catalogCardSliders()
